.player {
  --primary: #FCFCFC;
  --primaryHover: #3C28EA;

  position: fixed;
  min-height: 100px;
  width: 100%;
  bottom: 0;
  display: flex;
  background-color: #1B1B1B;
}

.player__current-song {
  padding: 5px;
  display: flex;
  align-items: center;
}

.player__current-song__text {
  display: none;
}

.player__control-panel {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.player__buttons {
  display: flex;
  margin-bottom: 10px;
  gap: 15px;
}

.player__buttons > button {
  color: var(--primary);
  background: none;
  border: none;
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

button.play-pause-btn {
  font-size: 32px;
}

.player__buttons > button:hover {
  color: var(--primaryHover)
}

.player__timeline-waveform {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  gap: 10px;
}

.player__timeline-waveform > div {
  color: var(--primary);
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
}

input[type=range] {
  --bar-bg: #3E3E3E;
  --seek-before-width: 0;
  --seek-before-color: #3C28EA;
  --knobby: #888888;
  --selectedKnobby: #2DEAF6;

  appearance: none;
  background: var(--bar-bg);
  border-radius: 10px;
  position: relative;
  width: 50%;
  max-width: 250px;
  height: 9px;
  outline: none;
}

/* progress bar - safari */

input[type=range]::-webkit-slider-runnable-track {
  background: var(--bar-bg);
  border-radius: 10px;
  position: relative;
  width: 50%;
  max-width: 250px;
  height: 9px;
  outline: none;
}

/* progress bar - firefox */

input[type=range]::-moz-range-track {
  background: var(--bar-bg);
  border-radius: 10px;
  position: relative;
  width: 50%;
  max-width: 250px;
  height: 9px;
  outline: none;
}

input[type=range]::-moz-focus-outer {
  border: 0;
}

/* progress bar - chrome and safari */

input[type=range]::before {
  content: "";
  height: 9px;
  width: var(--seek-before-width);
  background-color: var(--seek-before-color);
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  cursor: pointer;
}

/* progress bar - firefox */

input[type=range]::-moz-range-progress {
  background-color: var(--seek-before-color);
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  height: 9px;
}

/* knobby -chrome and safari */

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 15px;
  width: 15px;
  border-radius: 50%;
  border: none;
  background-color: var(--knobby);
  cursor: pointer;
  position: relative;
  margin: -3px 0 0 0;
  z-index: 3;
}

/* knobby while dragging - chrome and safari */

input[type=range]:active::-webkit-slider-thumb {
  transform: scale(1.2);
  background: var(--selectedKnobby);
}

/* knobby firefox */

input[type=range]::-moz-range-thumb {
  height: 15px;
  width: 15px;
  border-radius: 50%;
  border: transparent;
  background-color: var(--knobby);
  cursor: pointer;
  position: relative;
  z-index: 3;
}

/* knobby while dragging - firefox */

input[type=range]:active::-moz-range-thumb {
  transform: scale(1.2);
  background: var(--selectedKnobby);
}

@media screen and (min-width:1024px) {

  .player__current-song {
    position: absolute;
  }

  .player__current-song__text {
    display: block;
    min-width: 200px;
  }
  
  .player__current-song__text {
    margin-left: 1rem;
    line-height: 1;
    width: 100%;
  }
  
  .player__current-song__text > h1 {
    font-size: 1.2rem;
    color: #fff;
  }
  
  .player__current-song__text > p {
    font-size: 0.9rem;
    margin-top: 0.5rem;
    font-weight: 500;
    color: gray;
  }
}